import React, { Component } from 'react';

import ImgCom from '../Components/ImageCom'

import Pic1 from '../TestPics/WhatsApp Image 2020-12-13 at 6.28.55 PM.jpeg'
import Pic2 from '../TestPics/WhatsApp Image 2020-12-13 at 6.28.56 PM (1).jpeg'
import Pic3 from '../TestPics/WhatsApp Image 2020-12-13 at 6.28.56 PM.jpeg'
import Pic4 from '../TestPics/WhatsApp Image 2020-12-13 at 6.28.57 PM.jpeg'
import Pic5 from '../TestPics/WhatsApp Image 2020-12-13 at 6.28.58 PM (1).jpeg'
import Pic6 from '../TestPics/WhatsApp Image 2020-12-13 at 6.28.58 PM.jpeg'
import Pic7 from '../TestPics/WhatsApp Image 2020-12-13 at 6.28.59 PM (1).jpeg'
import Pic8 from '../TestPics/WhatsApp Image 2020-12-13 at 6.28.59 PM.jpeg'


class TestComp extends Component {

  state = { 
      index: 0,
      picList : [Pic1,Pic2,Pic3,Pic4,Pic5,Pic6,Pic7,Pic8]
  }

  componentDidMount() 
  {
    
  }

  onClickNext= () => {
    if (this.state.index + 1 === this.state.picList.length) {
      this.setState({
        index: 0
      })
    } else {
      this.setState({
        index: this.state.index + 1
      })
    }
  }

  onClickPrevious= () => {
    if (this.state.index - 1 === -1 ){    this.setState({
        index: this.state.picList.length - 1
      })
    } else {
      this.setState({
        index: this.state.index - 1
      })
    }
  }

  

  render() { 
      return (
      <div className="Album">
        
        <ImgCom className="Photo" src={this.state.picList[this.state.index]}></ImgCom>

        <p className="Title"> {this.state.picList[this.state.index]}</p>
        

        <a href={this.state.picList[this.state.index]} download={this.state.picList[this.state.index]}>Download</a>

        <div className="Buttons">
          <button className="PrevButton"  onClick={this.onClickPrevious}> Previous </button>
          
          <button className="NextButton" onClick={this.onClickNext}> Next </button>     
        </div>

      </div> );
  }
}
 
export default TestComp;